import React, { Component } from "react"
import styled from "styled-components"
import { rhythm } from "../utils/typography"
import FloatingLabelInput from "react-floating-label-input"
import { Button } from "./background"

class EmailSignup extends Component {
  state = {
    name: this.props.name,
    email: this.props.email,
    hp: "",
    gdpr: true,
    success: false,
  }

  handleChange = e => {
    const { name, type, value } = e.target
    const val = type === "number" ? parseFloat(value) : value
    this.setState({ [name]: val })
  }

  toggleGDPR = e => {
    this.setState({ gdpr: !this.state.gdpr })
  }

  sendSubscribeRequest = async payload => {
    return fetch("https://mail.stupidsystems.com/subscribe", {
      method: "POST",
      mode: "no-cors",
      credentials: "same-origin",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded",
      },
      body: new URLSearchParams({
        ...this.state,
        list: "w11veSoyUbYWx8PANkEe6w",
        boolean: true,
        subform: true,
      }),
    })
  }

  render() {
    if (this.state.success) {
      return (
        <div id="newsletter">
          <h3>Thank you!</h3>
          <p className="center">
            Thanks for signing up! I'll do my best to keep your interest.
            Otherwise, you can unsubscribe at any time. Just look for the
            unsubscribe link in your email.
          </p>
        </div>
      )
    }
    return (
      <div id="newsletter">
        <h3
          style={{
            margin: "0",
            marginTop: 0,
          }}
        >
          Signup for my Newsletter
        </h3>
        <form
          style={{
            marginTop: rhythm(1),
          }}
          id="newsform"
          onSubmit={async e => {
            e.preventDefault()
            const res = await this.sendSubscribeRequest()
            console.log(res)
            if (!res) return
            this.setState({ success: true })
          }}
        >
          <label htmlFor="name">
            <FloatingLabelInput
              type="text"
              name="name"
              label="Name"
              id="name"
              onChange={this.handleChange}
              value={this.state.name || ""}
            />
          </label>
          <br />
          <label htmlFor="email">
            <FloatingLabelInput
              type="email"
              label="Email"
              name="email"
              id="email"
              onChange={this.handleChange}
              value={this.state.email || ""}
            />
          </label>
          <div style={{ display: "none" }}>
            <label htmlFor="hp">HP</label>
            <input
              type="text"
              name="hp"
              id="hp"
              onChange={this.handleChange}
              value={this.state.hp || ""}
            />
          </div>
          <div style={{ width: "100%", marginTop: rhythm(0.5) }}>
            <Button style={{}}>
              <button
                type="submit"
                name="submit"
                id="submit"
                className="center"
              >
                Subscribe
              </button>
            </Button>
          </div>
        </form>
        <p>
          By clicking the above, I consent to Andrew contacting me via email
          using the above information for news, updates, and marketing.
        </p>
      </div>
    )
  }
}
export default EmailSignup
